nav {
    min-height: 10vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    button{
        border: none;
        cursor: pointer;
        border: 2px solid rgb(65,65,65);
        padding: 0.5rem;
        transition: all 0.3s ease;
        &:hover{
            background: rgb(189, 181, 181);
        }
    }
}